<template>
  <div>
    <mt-popup
      v-model="popupVisible"
      popup-transition="popup-fade">
      <div class="payType">
        <div class="count_top header">
          <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
          <span>支付方式</span>
        </div>
        <section @click="changeType('0')">
          <p>
            <img src="../../assets/person/pay-01.png" alt="">
            <span>余额支付</span>
          </p>
          <p><img src="../../assets/shopImage/right.png" alt=""> </p>
        </section>
        <section @click="changeType('1')">
          <p>
            <img src="../../assets/person/pay-02.png" alt="">
            <span>第三方支付</span>
          </p>
          <p><img src="../../assets/shopImage/right.png" alt=""> </p>
        </section>
      </div>
    </mt-popup>
  </div>
</template>
<script>
  import { Popup } from 'mint-ui';
export default {
  data () {
    return {
      popupVisible:false,
      value: '',
      options: [
        {
          label: '余额支付',
          value: '0'
        },
        {
          label: '其他支付',
          value: '1'
        }
      ],
    }
  },
  methods:{
    back(){
      this.popupVisible = false;
    },
    changeType(val) {
      console.log()
      this.popupVisible = false;
      this.$emit('show', val);
    }
  }
}
</script>
<style scroped>
  @import "../../../static/header.css";
  .payType {
    max-width: 750px;
    margin: 0 auto;
    height: 100vh;
    width: 100vw;
    background-color: #f2f2f2;
  }
  .payType section {
    border: 1px solid #f2f2f2;
    height: 1rem;
    line-height: 1rem;
    background-color: #ffffff;
    padding: 0 0.3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .payType section img:first-child {
    width: 0.52rem;
  }
  .payType section img:last-child {
    width: 0.18rem;
  }
  .payType section span {
    font-size: 0.3rem;
    color: #111111;
    vertical-align: middle;
  }
</style>



